@import "https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap";
@import "https://fonts.googleapis.com/css2?family=IM+Fell+Great+Primer&display=swap";
@import "https://fonts.googleapis.com/css2?family=Playball&display=swap";
:root {
  --pallete0: #a09c79;
  --pallete1: #9d002b;
  --pallete2: #437155;
  --pallete3: #cea19b;
  --pallete4: #3c2f29;
  --pallete5: #c76862;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: IM Fell Great Primer, Fredericka the Great, cursive;
}

img {
  max-width: 100%;
  display: block;
}

.entry-header {
  width: 100vw;
  height: 100vh;
  color: #fff;
}

.entry-img-wrapper img {
  object-fit: unset;
  height: 100vh;
  width: 100vw;
  position: relative;
}

.entry-header nav {
  width: 100%;
  position: absolute;
  top: 0;
}

.menue {
  background-color: #51465b79;
  border-bottom: .2rem solid #601212;
  justify-content: space-evenly;
  padding: 1rem;
  font-size: 1.2rem;
  list-style: none;
  display: flex;
}

.menue li:hover {
  color: var(--pallete3);
  font-size: 1.5rem;
}

.entry-header h1 {
  font-size: 1.8rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.entry-header h1:hover {
  color: var(--pallete1);
}

.explore-btn {
  color: #fff;
  background-color: #60121298;
  border: .01rem solid #fff;
  border-radius: .6rem;
  padding: .8rem;
  font-size: 1rem;
  text-decoration: none;
  display: block;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.explore-btn:hover {
  background-color: var(--pallete5);
  color: #000;
}

.flex-container {
  background-color: var(--pallete0);
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.shopping-header {
  width: 90%;
  border-bottom: .1rem solid #000;
  align-items: center;
  display: flex;
}

.shopping-header a {
  width: 50%;
  color: #000;
  font-family: Playball, cursive;
  font-size: 1.5rem;
  text-decoration: none;
}

.filters-container {
  width: 50%;
  color: #000;
  justify-content: space-evenly;
  align-items: flex-end;
  display: flex;
}

.filter {
  flex-direction: row;
  padding: .3rem;
  display: flex;
}

.filter label {
  padding: .3rem;
}

.filter select {
  background-color: var(--pallete2);
  color: #fff;
  border-radius: 1rem;
  padding: .3rem;
}

.shopping-main-container {
  width: 90%;
  margin: 1rem 0;
}

.shopping-sidebar {
  z-index: 1;
  background-color: var(--pallete2);
  color: #fff;
  border-radius: 0 20% 20% 0;
  position: fixed;
  top: 50%;
  left: 0;
}

.sidebar-nav ul {
  padding: .5rem .2rem .5rem 0;
}

.sidebar-nav ul li {
  padding: .5rem .6rem;
  list-style: none;
}

.sidebar-nav ul li:hover {
  color: var(--pallete4);
}

.products-container {
  flex-flow: wrap;
  justify-content: space-evenly;
  display: flex;
}

.colummn {
  width: 100%;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 1rem;
  display: flex;
}

.product {
  width: 100%;
  color: #000;
  border-radius: 1rem;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  transition: all .6s ease-in-out;
  display: flex;
  position: relative;
  box-shadow: 0 5px 15px #00000059;
}

.product img {
  border-radius: 1rem;
  transition: all .6s ease-in-out;
}

.product:hover img {
  z-index: 3;
  transform: scale(1.2)translateY(10px);
}

.product:hover {
  background-color: var(--pallete0);
}

.product h3 {
  color: #fff;
  z-index: 3;
  background-color: #9d002a7a;
  border-radius: 1rem;
  padding: .5rem;
  transition: all 3s ease-in-out;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
}

.product:hover h3 {
  color: #e1dada;
  background-color: #9d002acb;
  position: absolute;
  transform: scale(.7);
}

@media screen and (max-width: 480px) {
  .filters-container {
    flex-direction: column;
  }
}

@media screen and (min-width: 480px) and (max-width: 768px) {
  .colummn {
    width: 50%;
  }
}

@media screen and (min-width: 768px) {
  .entry-header h1 {
    font-size: 3rem;
  }

  .colummn {
    width: 33.33%;
  }
}

